import './App.css';
//=====Task 1=====////
import Profile from './components/profile/social-profile';
import card from './social-profile/user.json';
//=====Task 2=====////
import Statistics from './components/statistics/statistics';
import statsData from './statistics/statistical-data.json';
//=====Task 3=====////
import FriendList from './components/friendlist/friend-list';
import friends from './friend-list/friends.json';
//=====Task 4=====////
import TransactionHistory from './components/transactions/transaction-history';
